import React from "react";
import { ResponsiveNeoGraph } from '@components';
import styled from 'styled-components';


const StyledForm = styled.section`
  text-align: center;

`;


const NeoGraphContainer = (props) => {
    const [name, setName] = React.useState('Makofane K')
    const [weight, setWeight] = React.useState(1)
    const [queryName, setqueryName] = React.useState('Makofane K')
    const [queryWeight, setqueryWeight] = React.useState(1)

    const containerId = props.containerId
    const neo4jUri = props.neo4jUri
    const neo4jUser = props.neo4jUser
    const neo4jPassword = props.neo4jPassword


    const handleChangeN = (e) => {
        setName(e.target.value);
    }

    const handleChangeW = (e) => {
        setWeight(e.target.value);
    }

    const handleSubmit = (e) => {
        setqueryName(name);
        setqueryWeight(weight)
        e.preventDefault()
    }

    React.useEffect(() => {
            let TheMeat =  <ResponsiveNeoGraph
                    containerId={containerId}
                    neo4jUri={neo4jUri}
                    neo4jUser={neo4jUser}
                    neo4jPassword={neo4jPassword}
                    searchname={queryName}
                    collabweight={queryWeight}/>

        document.getElementById('ContainerForGraph').replaceWith(TheMeat)
    }, [queryName, queryWeight]);

        return (<StyledForm>
              <form onSubmit = {handleSubmit}>
                  Researcher  &nbsp;
                  <input type="text" value={name} onChange={handleChangeN} /> &emsp;
                    Minimum Strength of Connections  &nbsp;
                    <select  name="weight" id = "weight" value = {weight} onChange={handleChangeW}>
                        <option value="1" selected>1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4">4</option>
                        <option value="5">5</option>
                        <option value="10">10</option>
                        <option value="20">20</option>
                    </select> &emsp;
                <input type="submit"/>
                {/*<input type='text' list='listid'/>*/}
                {/*    <datalist id='listid'>*/}
                {/*        <option  value='value1'/>*/}
                {/*        <option  value='value2'/>*/}
                {/*    </datalist>*/}

            </form>

            <div className="App" id = "ContainerForGraph">
            </div>

        </StyledForm>);

}

export default NeoGraphContainer;